const DC = 'USD'; //default currency
const DD = '2-digit'; //default digits
const DL = 'en-US'; //default locale

type Dateable = string | number | Date | null;
type Locales = string | string[];

export function currency(value: any, locales: Locales = DL, currency: string = DC) {
  return parseFloat(value)?.toLocaleString?.(locales, {style: 'currency', currency});
}

export function date(value: Dateable, locale: Locales = DL) {
  return toDate(value).toLocaleDateString(locale, {day: DD, month: DD, year: 'numeric', timeZone: 'UTC'});
}

export function formattedDate(value: Dateable, locale: Locales = DL) {
  const responseDate = date(value, locale);
  return responseDate || '';
}

export function datetime(value: Dateable, locales: Locales = DL) {
  return toDate(value).toLocaleString(locales, {day: DD, month: DD, year: 'numeric', hour: DD, minute: DD, hour12: true});
}

const dateISO = (date: Dateable) => toDate(date)?.toISOString?.() || null;
export const dateToISO = (date: Dateable, full: boolean = false) => (full ? dateISO(date) : dateISO(date)?.substr?.(0, 10)) || null;

export function toDate(value?: Dateable) {
  if (!value) return new Date();
  const date = value instanceof Date ? value : new Date(value);
  return isNaN(date.getTime()) ? new Date() : date;
}

export function unixToDateTime(value: number) {
  return datetime(new Date(value * 1000));
}
